.app-wrapper {
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-template-areas:
            "header header"
            "nav content";
}

.app-wrapper-content {
    grid-area: content;
    background-color: #fce3c2;
}

.app-wrapper-content img {
    width: 1000px;
}




